.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow: hidden;

  &--no-scrollbar {
    height: 100vh;
    /* stylelint-disable-next-line */
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
  }
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.hidden-mobile {
  @include vp--767 {
    display: none;
  }
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.text-accent {
  color: $color-blue;
}

.custom-scrollable {
  scrollbar-color: $color-blue $color-gray-bg;
  scrollbar-width: thin;
}

.hover-main {
  transition: color $trans-default;

  @media (hover: hover), screen and (min-width: 768px) {
    &:hover,
    &:focus {
      color: $color-detail-blue;
    }

    &:focus-visible {
      outline: none;
    }
  }
}

.hover-secondary {
  transition: color $trans-default;

  @media (hover: hover), screen and (min-width: 768px) {
    &:hover,
    &:focus {
      border: 1px solid $color-text-dark-grey;
      color: $color-default-white;
    }

    &:focus-visible {
      outline: none;
    }
  }
}
