@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin non-touch {
  @media (hover: hover) and (pointer: fine) {
    @content;
  }
}

//    
//---------------------------------
@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin vp--1919 {
  @media (max-width: $vp--1920 - 1) {
    @content;
  }
}

